<template>
  <div class="documentation pad">
      <div class="router-menu">Вы здесь: <div class="link" @click="goTo('/')"> Главная</div> / <div class="link"> Документы</div>  </div>
      <div class="main__block-title left">ДОКУМЕНТЫ</div>
      <div class="documentation__items">
        <div class="documentation__item">
          <a href="/static/documentation/svidetelstvo-o-gosregistraczii-2015.pdf" ><img src="/static/documentation/svid.png" alt="" class="documentation__item-img"></a> 
          <div class="documentation__item-name">Свидетельство о регистрации</div>
        </div>
        <div class="documentation__item">
          <a href="/static/documentation/sertifikat-sootvetstviya-pocc-ru.sp29.n00967.pdf" download="sertifikat-sootvetstviya-pocc-ru.sp29.n00967.pdf"><img src="/static/documentation/sert.png" alt="" class="documentation__item-img"></a>
          <div class="documentation__item-name">Сертификат соответствия</div>
        </div>
        <div class="documentation__item">
          <a href="/static/documentation/deklaracziya-o-sootvetstvii-2016.pdf" download="deklaracziya-o-sootvetstvii-2016.pdf"><img src="/static/documentation/decl.png" alt="" class="documentation__item-img"></a>
          <div class="documentation__item-name">Декларация о соответствии</div>
        </div>
      </div>
  </div>
</template>

<script>

export default {
  name: 'Documentation',
  components: {
  },
  methods:{
    goTo(val){
      this.$router.push(val)
    }
  }
}
</script>

<style>
  .documentation{
      position: relative;
      width: 90%;
  }
  .documentation__items{
      display:inline-flex;
  }
  .documentation__item{
    width: 20%;
    margin: 0 0 30px 0;
  }
  .documentation__item-img{
    width: 80%;
  }
  .documentation__item-name{
    font-weight: 300;
    font-size: 24px;
    width: 180px;
    text-align: left;
    margin: 60px 0 0 35px;
  }
</style>



