<template>
  <div class="products">
    <div class="router-menu pad">Вы здесь: <div class="link" @click="goTo('/')"> Главная</div> / <div > Культуры</div>  </div>
      <div class="main__block">
      <div class="main__block-title left pad">КУЛЬТУРЫ</div>
        <div class="main__item-block">
        <div class="main__item-block">
        <div class="veget link" v-for="element of veget" :key="element.id" @click="chooseProd(element.id)">
          <div class="veget-img"><img :src="element.img" alt="" class="veget-img ico"></div>
          <div class="veget-name">{{element.name}}</div> 
        </div>
      </div>
      </div>
    </div>

    <div class="text-block">
        <div class="text-block__col">
            <ul>
                <li class="par2">Микроэлементы — это необходимая составляющая при выращивании качественного урожая. Они являются незаменимой частью питания, способствуют повышению иммунитета растений, снижают влияние стресса от применения пестицидов и неблагоприятных погодных факторов. Роль микроэлементов в растениях в основном заключается в том, что они являются катализаторами биохимических процессов, находясь в клетке в свободном виде или входя в состав некоторых ферментов. Каждый микроэлемент связан с несколькими ферментами, многие из которых отвечают за несколько биопроцессов в организме. При этом стоит понимать, что ферменты отвечают за фотосинтез, питание, цветение, иммунитет и прочие процессы в организме растения. 
                </li>
                <li class="par2">Применение комплекса удобрений МЕГАМИКС оказывает профилактическое воздействие на подавление болезней, позволяет реализовать потенциальную урожайность и качество выращиваемой продукции. Высокая эффективность этого удобрения отмечена и в неблагоприятные по метеоусловиям годы, за счет усиления фотосинтеза и содержания сахаров в клетках, что препятствует потере воды в засуху, замерзанию при заморозках и способствует регенерации при уже полученных повреждениях. 
                </li>
            </ul>
        </div>
        <div class="text-block__col">
            <ul>
                <li class="par2">
                    При использовании МЕГАМИКС усиливаются процессы азотфиксации, что является дополнительным источником азота для культуры. При этом отдельно стоит отметить усиление несимбиотической азотфиксации на небобовых культурах. 
                </li>
                <li class="par2">
                    При обработке семян. МЕГАМИКС усиливает окислительно-восстановительные реакции и интенсивность корневых выделений, в частности растворяющих реагентов типа яблочной, щавелевой, уксусной и других кислот, а также ферментов и метаболитов для корневого питания. Эти реагенты превращают находящиеся в почве элементы питания в легкую для усвоения ионную форму и вместе с микроорганизмами образуют ризосферу вокруг корневых сосущих волосков, откуда растение поглощают необходимые элементы питания. 
                </li>
                <li class="par2">
                    При листовых подкормках. МЕГАМИКС восполняет недостаток биогенных микроэлементов в период вегетации. Питательные вещества, проникая в межклеточное пространство и проводящую систему, активно включаются в метаболизм растения. Повышается эффективность фотосинтеза, дыхания и ростовых процессов. Увеличиваются корневые выделения, которые стимулируют полезные почвенные микроорганизмы в ризосфере.
                </li>  
            </ul>
        </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Products',
  components: {
  },
  data () {
    return {
      veget: [
        {
          id: 0,
          name: 'Зерновые',
          img: 'static/cultures/Rye.png'
        },
        {
          id: 1,
          name: 'Подсолнечник',
          img: 'static/cultures/sunflower.png'
        },
        {
          id: 2,
          name: 'Кукуруза',
          img: 'static/cultures/corn.png'
        },
        {
          id: 3,
          name: 'Сахарная свекла',
          img: 'static/cultures/shugars.png'
        },
        {
          id: 4,
          name: 'Картофель',
          img: 'static/cultures/pottatoo.png'
        },
        {
          id: 5,
          name: 'Зернобобовые',
          img: 'static/cultures/bobs.png'
        },
        {
          id: 6,
          name: 'Рапс',
          img: 'static/cultures/raps.png'
        },
        {
          id: 7,
          name: 'Лён',
          img: 'static/cultures/len.png'
        },
      ]
    }
  },
  methods:{
    chooseProd(val){
          this.$router.push('/culture/'+val)
      },
    goTo(val){
      this.$router.push(val)
    }
  }
}
</script>

<style>
    .main__block-title{
        font-weight: 100;
    }
    .text-block{
        display: flex;
        margin: 0 5%;
    }
    .text-block__col{
        flex: 1;
    }
    ul{
        margin-bottom: 60px;
    }
    .par2{
      margin-bottom: 20px;
      text-align: left;
    }
    .ico{
        width: 80%;
        height: 80%;
    }
</style>



