<template>
  <div class="spinner">
    <div class="spinner__img"> <img class="spinner__img" :src="data[num].img" alt=""></div>
    <div class="spinner__discription">    </div>
    <div class="spinner__discription-info">
            <div class="spinner__discription-info-img-block">  <img src="static/img/Symbol2–5.svg" alt="" class="spinner__discription-info-img"> </div>  
            <div class="spinner__discription-info-firstline">{{data[num].title}}</div>  
            <div class="spinner__discription-info-secondline">{{data[num].name}}</div>
            <div class="spinner__discription-info-thirdline" v-for="item of data[num].discription" :key="item"> 
                <div class="circle"></div> <div class="item">
                    <div v-for="el of item" :key="el">{{el}}</div>
                </div>
            </div>
        </div>
    <div class="spinner__marker">
        <div class="spinner__marker-item" v-for="(circle, index) of 3" :key="circle" :class="{ dotted: data[index].isActive }" @click="changeSlide(index, num)"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Spinner',
  components: {
  },
  data () {
    return {
        data: [
            {
                title: 'Удобрения',
                name: 'МЕГАМИКС',
                discription: [
                    ['активизация иммунитета', 'и стрессоустойчивости растений'],
                    [' увеличение коэффициента', 'использования минеральных удобрений']
                ],
                img: '/static/slider/1-5.png',
                isActive: true
            },
            {
                title: 'Удобрения',
                name: 'МЕГАМИКС',
                discription: [
                    ['прирост качественного и урожая']
                ],
                img: '/static/slider/1-2.png',
                isActive: false
            },
            {
                title: 'Удобрения',
                name: 'МЕГАМИКС',
                discription: [
                    ['эффективные и качественные удобрения', 'для сельского хозяйства']
                ],
                img: '/static/slider/1-4.png',
                isActive: false
            },
        ],
        num: 0
    }
  },
  mounted(){
      
      setInterval(this.rotateSlide, 5000)
  },
  methods:{
      changeSlide(val1, val2){
          this.num = val1
          this.data[val2].isActive = true
          this.data[val1].isActive = true
      },
      rotateSlide(){
          this.data[this.num].isActive = false
          this.num +=1
          
          if (this.num === 3){
              this.num = 0
              
          }
          this.data[this.num].isActive = true
      }
  }
}
</script>


<style>
    @import url('https://fonts.googleapis.com/css?family=Roboto:900&display=swap&subset=cyrillic,cyrillic-ext');

  .spinner{
      width: 100%;
      margin-top: 30px;
      margin:  0 0 30px 0;
  }
  .spinner__img{
      width: 100%;
      background: grey;
      height: 675px;
  }
  .spinner__marker-item{
      width: 10px;
      height: 10px;
      border: 2px solid #c2bcbc;
      background: white;
      border-radius: 50%;
      margin: 10px;
      display: inline-block;
  }
  .spinner__marker-item:hover{
      cursor: pointer;
  }
  .spinner__discription{
      position: absolute;
      right: 0;
      width: 40%;
      height: 675px;
      margin-top: -675px;
      background-color: rgba(255, 255, 255, 0.1);
      line-height: 1; 
      -webkit-backdrop-filter: blur(5px);
      backdrop-filter: blur(5px);  
      max-width: 50%;
      max-height: 50%;
      padding: 20px 40px; 
  }
  .spinner__discription-info{
      opacity: 1;
      position: absolute;
      right: 0;
      width: 40%;
      height: 715px;
      margin-top: -715px; 
      color: white;
  }
  .spinner__discription-info-firstline{
      text-align: left;
      margin-left: 60px;
      font-size: 36px;
  }
  .spinner__discription-info-secondline{
      text-align: left;
      margin: 45px 0 20px 60px;
      font-size: 82px;
      font-weight: bolder;
  }
  .spinner__discription-info-thirdline{
      text-align: left;
      margin-left: 60px;
      font-size: 18px;
      vertical-align: top;
  }
  .circle{
      position: relative;
      top: 0;
      width: 5px;
      height: 5px;
      margin: 30px 5px 0 0;
      background: white;
      display: inline-block;
      border-radius: 50%;
  }
  .item{
      display: inline-block;
      vertical-align: text-top;
  }
  .spinner__discription-info-img-block{
      position: relative;
      text-align: left;
  }
  .spinner__discription-info-img{
      
      width: 100px;
      height: 100px;
      margin: 130px 0 0 60px;
  }
  .dotted{
      background:#c2bcbc;
  }
</style>


