<template>
  <div class="MobSpinner">
    <img class="MobSpinner__img" :src="data[num].img" alt="">
    <div class="MobSpinner-textblock" :style="{background: data[num].backGr}">
        <div class="MobSpinner__title">{{data[num].title}}</div>
        <div class="MobSpinner__name">{{data[num].name}}</div>
        <ul style="color: white">
            <li v-for="item of data[num].discription" :key='item'>
                <div v-for="elem of item" :key='elem' class="MobSpinner_dicr">{{elem}}</div>
            </li>
        </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MobSpinner',
  components: {
  },
  data () {
    return {
        data: [
            {
                title: 'Удобрения',
                name: 'МЕГАМИКС',
                discription: [
                    ['активизация иммунитета', 'и стрессоустойчивости растений'],
                    [' увеличение коэффициента', 'использования минеральных удобрений']
                ],
                img: '/static/slider/1-5.png',
                isActive: true,
                backGr: '#639F2F'
            },
            {
                title: 'Удобрения',
                name: 'МЕГАМИКС',
                discription: [
                    ['прирост качественного и урожая']
                ],
                img: '/static/slider/1-2.png',
                isActive: false,
                backGr: '#BFA331'
            },
            {
                title: 'Удобрения',
                name: 'МЕГАМИКС',
                discription: [
                    ['эффективные и качественные удобрения', 'для сельского хозяйства']
                ],
                img: '/static/slider/1-4.png',
                isActive: false,
                backGr: '#3563AA'
            },
        ],
        num: 0
    }
  },
  mounted(){
      
      setInterval(this.rotateSlide, 5000)
  },
  methods:{
      changeSlide(val1, val2){
          this.num = val1
          this.data[val2].isActive = true
          this.data[val1].isActive = true
      },
      rotateSlide(){
          this.data[this.num].isActive = false
          this.num +=1
          
          if (this.num === 3){
              this.num = 0
              
          }
          this.data[this.num].isActive = true
      }
  }
}
</script>


<style>
    .MobSpinner__img{
        width: 100%;
        margin-top: 60px;
    }
    .MobSpinner-textblock{
        padding: 20px 0 20px 26px;
    }
    .MobSpinner__title{
        font-size: 22pt;
        color: white;
        text-align: left;
    }
    .MobSpinner__name{
        font-weight: 700;
        font-size: 55pt;
        color: white;
        text-align: left;
    }
    .MobSpinner_dicr{
        font-size: 12pt;
        color: white;
        text-align: left;
    }
</style>


