<template>
  <div class="products">
    <div class="router-menu pad">Вы здесь: <div class="link" @click="goTo('/')"> Главная</div> / <div> Продукты</div> </div>
      <div class="main__block">
      <div class="main__block-title pad left">ПРОДУКЦИЯ</div>
        <div class="main__item-block">
        <div class="goods link" v-for="element of goods" :key="element.id" @click="chooseProd(element.id)">
          <div class="goods-img"><img :src="element.img" alt="" class="goods-img"></div>
          <div class="goods-name">МЕГАМИКС&nbsp;-&nbsp;{{element.name}}</div> 
        </div>
      </div>
    </div>

    <div class="text-block">
        <div class="text-block__col">
            <ul>
                <li class="par2">это высокоэффективное комплексное жидкое минеральное удобрение, в основе которого богатый состав микро- и макроэлементов. 
                    Часть микроэлементов находятся в хелатной форме, легко усваиваемой растениями. 
                </li>
                <li class="par2">это удобрение, содержащее сбалансированный комплекс микроэлементов, которые усиливают нужные процессы роста и развития культур. 
                </li>
                <li class="par2">оказывает сильнейшее воздействие на жизненные процессы растения при небольшой норме применения.</li>
            </ul>
        </div>
        <div class="text-block__col">
            <ul>
                <li class="par2">
                    рекомендуется использовать для предпосевной обработки семян и листовых подкормок. Удобрение применяется совместно с пестицидами и другими агрохимикатами. 
                </li>
                <li class="par2">
                    дает существенную прибавку урожая и повышает его питательную ценность, обогащая его важными микроэлементами Se, Cu, Fe, Zn и другими.
                </li>
            </ul>
        </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Products',
  components: {
  },
  data () {
    return {
      goods: [
        {
          id: 0,
          name: 'СЕМЕНА',
          img: 'static/tara/semena.png'
        },
        {
          id: 1,
          name: 'ПРОФИ',
          img: 'static/tara/profi.png'
        },
        {
          id: 2,
          name: 'БОР',
          img: 'static/tara/bor.png'
        },
        {
          id: 3,
          name: 'МАГНИЙ-ЦИНК',
          img: 'static/tara/Mg-Zn_2.png'
        },
        {
          id: 4,
          name: 'СЕРА',
          img: 'static/tara/Sera_2.png'
        },
        {
          id: 5,
          name: 'АЗОТ',
          img: 'static/tara/azot.png'
        },
        {
          id: 6,
          name: 'ФОСФОР',
          img: 'static/tara/Fosfor.png'
        },
        {
          id: 7,
          name: 'КАЛИЙ',
          img: 'static/tara/kaliy 1.png',
          imgBan: 'static/tara/kaliyBan.png',
          info: [
            {
              class: 'par',
              text: '«МЕГАМИКС – КАЛИЙ» дополняет основное питание и временно его заменет, когда корневое питание затруднено почвенной засухой, низкой температурой почвы, стрессами, в частности от пестицидов. '
            },
            {
              class: 'par',
              text: 'Акцент на высокое содержание комплекса микроэлементов в «МЕГАМИКС – КАЛИЙ» позволяет данному удобрению повысить как эффективность калия в составе, так и стимулировать корневое питание и снятие стрессов. '
            },

            {
              class: 'par',
              text: 'Благодаря свойству калия связывать и структурировать воду в клетках растения обработки «МЕГАМИКС – КАЛИЙ» снижают повреждение морозами на переросших озимых культурах и способствуют удержанию влаги при засухе. '
            },
            {
              class: 'par',
              text: 'Одной из проблем интенсивных культур является снижение продуктивности в период сильной жары и засухи. Калий в засушливый период способствует разжижению клеточного сока и оттоку питательных веществ в запасающие органы.'
            },
            
          ]
        },
      ]
    }
  },
  methods:{
    chooseProd(val){
          this.$router.push('/product/'+val)
    },
    goTo(val){
      this.$router.push(val)
    }
  }
}
</script>

<style>
    .main__block-title{
        font-weight: 100;
    }
    .text-block{
        display: flex;
        margin: 0 5%;
    }
    .text-block__col{
        flex: 1;
    }
    ul{
        margin-bottom: 60px;
    }
    .par2{
      margin-bottom: 20px;
      text-align: left;
    }
</style>



