<template>
  <div id="app">
    <div class="desctop">
      <HeaderMenu2></HeaderMenu2>
      <router-view/>
      <BottomMenu></BottomMenu>
    </div>
    <div class="mobile">
      <router-view/>
    </div>
  </div>
</template>

<script>
import BottomMenu  from '@/components/BottomMenu/BottomMenu'
import HeaderMenu2 from '@/components/HeaderMenu2/HeaderMenu2'
import Search from '@/components/Search/Search'
import Vue from 'vue'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

// Install BootstrapVue
Vue.use(BootstrapVue)
// Optionally install the BootstrapVue icon components plugin
//Vue.use(IconsPlugin)

export default {
  name: 'App',
  components: {
    BottomMenu,
    HeaderMenu2,
    Search
  },
}
</script>

<style>
  @import url('https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900&display=swap&subset=cyrillic');

  #app{
    text-align: center;
    font-family: 'Roboto';
    letter-spacing: 0; 
    color: #4D4F51; 
    margin: 0;
    padding: 0; 
    overflow: hidden;
  }
  body{

    overflow-x:hidden;
    overflow-y: auto; 
  }
  .main__block-title{
    font-size: 30px;
    color: #4D4F51;
    margin:0 0 20px 0;
    letter-spacing: 1.9px;

       font-weight: 300;
  }
  .main__block-subtitle{
    font-size: 18px;
    color: #4D4F51;
  }
  .main__line{
    position: relative;
    width: 120px;
    border-bottom: 1px solid #4e504f;
    left: 50%;
    margin: 30px 0 10px -60px; 
    margin-bottom: 90px;
    
  }
  .main__item-block{
    width: 90%;
    height: 100%;
    position: relative;
    left: 0;
    right: 0;
    margin: auto;
  }
  .goods{
    position: relative;
    width: 25%;
    display: inline-block;
    margin-bottom: 30px;

    vertical-align: top;
  }
  .goods-img{
    position: relative;
    width: 100%;
    height: 100%;
    margin: auto;
    vertical-align: top;
  }
  .goods-name{
    font-size: 20px;
  }
  .left{
    position: relative;
    text-align: left;
  }
  .veget{
    position: relative;
    width: 25%;
    display: inline-block;
    margin-bottom: 75px;
  }
  .veget-img{
    margin: auto;
    margin-bottom: 10px;
    position: relative;
    width: 80%;
    height: 80%;
  }
  .veget-name{
    font-size: 20px;
  }
  .link:hover{
    cursor: pointer;
  }
  .router-menu{
    width: 100%;
    display: inline-flex;
    text-align: left;
    color: #C2BCBC;
    font-weight: 300;
    font-size: 14px;
    margin: 20px 0;
  }
  .pad{
    width: 100%;
    padding-left: 5%;
  }
  

  .desctop {
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .mobile {
    display: none;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
  }
  @media screen and (max-width: 729px) {
  .desctop {
    display: none;
  }
  .mobile {
    display: inherit;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
  }
  
}
</style>



