<template>
  <div class="contacts pad">
      <div class="router-menu">Вы здесь: <div class="link" @click="goTo('/')"> Главная</div> / <div> Контакты</div>  </div>

      <div class="main__block-title left">КОНТАКТЫ</div>

      <div class="contacts__text left">
          <div class="par">Удобрения для сельского хозяйства, которые мы производим – это также наш вклад, пусть маленький, но, как мы считаем, очень своевременный в продовольственную безопасность России. </div>
          <div class="par">Мы также всегда готовы к сотрудничеству с единомышленниками, поставщиками и партнерами, крупными и мелкими агропредприятиями.</div>
      </div>

      <div class="contacts__map">
          <div class="map">
              <iframe   src="https://www.google.com/maps/embed?pb=!1m10!1m8!1m3!1d17685.843232132094!2d44.0768554!3d56.3517344!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sru!2sru!4v1583761052756!5m2!1sru!2sru" width="100%" height="450" frameborder="0" style="border:0;" allowfullscreen=""></iframe>
          </div>
          
          <div class="contacts_adress">
              <div class="par">
                  <img src="/static/img/place.svg" alt=""> 606440, Нижегородская обл., г. Бор, а/я 119 
              </div>
              <div class="par">
                  <img src="/static/img/phone.svg" alt="">
                  +7 (831) 592-73-52
              </div>
              <div class="par">
                  <img src="/static/img/mail.svg" alt="">
                  megamix52@mail.ru
              </div>
          </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Contacts',
  components: {
  },
  methods: {
      goTo(val){
      this.$router.push(val)
    }
  }
}
</script>

<style>
.contacts{
    width: 100%
}
 .contacts__text{
     width: 40%;
     margin-bottom: 60px;
 }
.contacts__map{
    display: inline-flex;
    text-align: justify;
    width: 100%;
}
.contacts_adress{
    font-size: 18px;
    margin: 0 0 0 60px;
    padding: 30px 0 0 0;
}
.map{
    width: 50%;
}
</style>



