<template>
  <div class="MobMenu">
     <div class="container" :class="{change: change}" @click="myFunction()">
         <div class="bar1"></div>
        <div class="bar2"></div>
        <div class="bar3"></div>
    </div> 
    <div class="MobMenu__menu" v-if="change">
        <div class="MobMenu__menu-item">О компании</div>
        <div class="MobMenu__menu-item">Продукция</div>
        <div class="MobMenu__menu-item">Культуры</div>
        <div class="MobMenu__menu-item">Статьи</div>
        <div class="MobMenu__menu-item">Документация</div>
        <div class="MobMenu__menu-item">Контакты</div>

    </div>
  </div>
</template>

<script>


export default {
  name: 'MobMenu',
  components: {
  },
  data () {
    return {
        change: false
    }
  },
  methods:{
    myFunction() {
        this.change = !this.change
    } 
  }
}
</script>

<style>
.MobMenu{
    width: 100%;
    text-align: center;
}
.container {
    position: relative;
    cursor: pointer;
    width: 52px;
    border: 2px solid #707070;
    border-radius: 10px;
    margin-top: 60px;
}

.bar1, .bar2, .bar3 {
    width: 30px;
    height: 2px;
    background-color: #707070;
    margin: 10px 0 10px -7px;
    transition: 0.4s;
}

/* Rotate first bar */
.change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px) ;
    transform: rotate(-45deg) translate(-9px, 6px) ;
}

/* Fade out the second bar */
.change .bar2 {
    opacity: 0;
}

/* Rotate last bar */
.change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
    transform: rotate(45deg) translate(-8px, -8px) ;
}
.MobMenu__menu-item{
    color:#4D4F51;
    margin: 40px 0;
}
</style>



