            {
              class: 'par2',
              text: 'В период формирования клубней «МЕГАМИКС – ПРОФИ» и «МЕГАМИКС – КАЛИЙ» положительно влияют на нарастание массы клубней и содержание крахмала, а «МЕГАМИКС – ФОСФОР» на лежкость продукции. '
            },
            {
              class: 'par2',
              text: 'При высоких дозах азотных удобрений эффективны обработки «МЕГАМИКС – ФОСФОР» и «МЕГАМИКС – СЕРА». '
            },
            {
              class: 'par2',
              text: 'При высокой интенсивности производства «МЕГАМИКС – БОР» в сочетании с другими удобрениями МЕГАМИКС положительно сказывается на развитии биомассы и лежкости клубней.'
            },
          ],
        },
        {
          id: 5,
          name: 'Зернобобовые',
          img: 'static/cultures/bobs.png',
          bold: false,
          imgBun: 'static/cultures/bobsBun.png',
          imgTbl: 'static/cultures/bobsTbl.png',
          info: [
            {
              class: 'par',
              text: 'Зернобобовые культуры особенно ценны своей способностью к симбиозу с азотфиксирующими (клубеньковыми) бактериями. Эта способность дает им ряд преимуществ в развитии и в то же время делает их более требовательными к условиям возделывания. Симбиотическая микрофлора очень чувствительна к факторам внешней среды, в том числе к минеральному питанию. Да и сами зернобобовые становятся более требовательны к применению удобрений при плохом развитии клубеньков. '
            },
            {
              class: 'par',
              text: 'Симбиотическая микрофлора чувствительна ко всему комплексу микроэлементов (Cu, Zn, Fe, Mn, B), но особенно к Mo и Co. Так же стоит отметить положительное влияние некоторых ультрамикроэлементов которые наиболее важны именно микроорганизмам, а не растениям (Se, Cr). Основное место обитания симбиотических бактерий – ризосфера и семена часто инокулируют для ее заселения. Ввиду этого особое значение получает обработка семян микроэлементами. '
            },
            {
              class: 'par',
              text: 'Сами же зернобобовые наиболее чувствительны к нехватке B и таких макроэлементов как K, S, Mg. Поскольку период цветения данных культур обычно растянут (цветение ярусами) и зерно формируется преимущественно за счет корневого питания (а не оттока запасов из вегетативной массы), то особое значение приобретает доступность питания на протяжении всей вегетации. Что делает эффективными некорневые подкормки.'
            }
          ],
          infoBot: [
            {
              class: 'par-ttl',
              text: 'Рекомендации'
            },
            {
              class: 'par2',
              text: 'Обработка семян удобрением «МЕГАМИКС – СЕМЕНА» активизирует начальное развитие как культуры, так и микрофлоры. Это вполне безопасно для препаратов на основе спор и метаболитов микроорганизмов. '
            },
            {
              class: 'par2',
              text: 'Отсутствие обработки семян микроэлементами в некоторой степени может компенсировать листовая подкормка в самые ранние фазы минимальными дозировками «МЕГАМИКС – ПРОФИ» или «МЕГАМИКС – АЗОТ». При этом микроорганизмам достается небольшая часть микроэлементов выделенных культурой через корни. '
            },
            {
              class: 'par2',
              text: 'Основные некорневые подкормки необходимо проводить в фазы розетки листьев-начала стеблевания, бутонизации и формирования-налива боба. Выбирайте препарат по ситуации: '
            },
            {
              class: 'par2',
              text: 'при внесении основных удобрений в почву и их доступности (наличие влаги) «МЕГАМИКС – ПРОФИ»; '
            },
            {
              class: 'par2',
              text: 'при нарушении корневого питания (почвенная засуха) «МЕГАМИКС – АЗОТ», «МЕГАМИКС – КАЛИЙ», «МЕГАМИКС – ФОСФОР»; '
            },
            {
              class: 'par2',
              text: 'при высокой интенсивности производства «МЕГАМИКС – СЕРА», «МЕГАМИКС – МАГНИЙ-ЦИНК», «МЕГАМИКС – БОР» в сочетании с другими удобрениями МЕГАМИКС.'
            },
          ],
        },
        {
          id: 6,
          name: 'Рапс',
          img: 'static/cultures/raps.png',
          bold: false,
          imgBun: 'static/cultures/rapsBun.png',
          imgTbl: 'static/cultures/rapsTbl.png',
          info: [
            {
              class: 'par',
              text: 'Рапс обладает большой жизненой силой — это позволяет в ряде регионов выращивать его как озимую культуру (что редкость для двудольных культур). Мощная корневая система является базой для обеспечения потенциала высоких темпов роста и большой биомассы, что делает рапс универсальной культурой, способной эффективно использовать как последействие удобрений предшественника так и удобрения внесенные непосредственно под урожай. Высокая физиологическая активность корневых выделений и ферментов внутри растения обуславливает сильную потребность рапса в широком ряде как макро, так и микроэлементов. '
            },
            {
              class: 'par',
              text: 'Во время активного развития важное значение имеет обеспечение рапса калием, магнием, цинком — для углеводного обмена, водного обеспечения и азотом, серой, молибденом — для азотного обмена. Обеспеченность доступным фосфором необходима в начальные фазы развития для акцента на развитии корневой системы (особенно у озимого рапса) и для поддержания азотно-фосфорного баланса в питании культуры. '
            },
            {
              class: 'par',
              text: 'Особенно отзывчив рапс на внесение бора, дефицит которого на ранних стадиях может привести к плохому развитию листьев, слабому ветвлению, малочисленной закладке цветочных почек и дуплистости корня, что особенно критично для озимого рапса. На более поздних стадиях — к опадению цветов, завязей и меньшему количеству семян в стручках.'
            },
          ],
          infoBot: [
            {
              class: 'par-ttl',
              text: 'Рекомендации'
            },
            {
              class: 'par2',
              text: 'Протравливание семян с удобрением «МЕГАМИКС – СЕМЕНА» активизирует ферменты и начальное развитие культуры, обеспечивает макроэлементами на первые 2–3 недели жизни, что дает повышение полевой всхожести и выживаемости проростков. Оптимальные фазы для листовой подкормки розетка - начало стеблевания и бутонизация, а нужные удобрения необходимо выбирать по ситуации: '
            },
            {
              class: 'par2',
              text: 'при доступности питания из почвы «МЕГАМИКС – ПРОФИ», можно с «МЕГАМИКС – БОР»; '
            },
            {
              class: 'par2',
              text: 'при отсутствии или недоступности питания из почвы «МЕГАМИКС – АЗОТ», «МЕГАМИКС – КАЛИЙ» и «МЕГАМИКС – ФОСФОР»; '
            },
            {
              class: 'par2',
              text: 'при высоких урожаях или дефиците соответствующего элемента в почве «МЕГАМИКС – СЕРА» и «МЕГАМИКС – МАГНИЙ-ЦИНК»; '
            },
            {
              class: 'par2',
              text: 'на озимом рапсе с осени «МЕГАМИКС – КАЛИЙ» для повышения перезимовки и «МЕГАМИКС – БОР» — для снижения растрескивания шейки корня; '
            },
            {
              class: 'par2',
              text: 'эффективность «МЕГАМИКС – БОР» выше в сочетании с другими удобрениями МЕГАМИКС.'
            },
          ],
        },
        {
          id: 7,
          name: 'Лён',
          img: 'static/cultures/len.png',
          bold: false,
          imgBun: 'static/cultures/lenBun.png',
          imgTbl: 'static/cultures/lenTbl.png',
          info: [
            {
              class: 'par',
              text: 'Лен — культура противоречивая в своей требовательности к технологии возделывания и при этом относительно низкой отзывчивости на удобрения. Такая двойственность обязывает уделять внимание не столько количеству, сколько качеству удобрений и технологии их применения. При слабой корневой системе лен обладает хорошей устойчивостью к засухе и высоким температурам. Это указывает на особенности его строения и сложность биохимических процессов. '
            },
            {
              class: 'par',
              text: 'Слабая корневая система в принципе определяет отзывчивость льна на локальное применение удобрений, в т. ч. на некорневые подкормки. При этом роль микроэлементов как катализатора биопроцессов, и питания в частности, сложно переоценить. Применение удобрений МЕГАМИКС на семена дает питание, которое повышает полевую всхожесть и энергию прорастания. Это особенно актуально для мелких семян льна. К тому же интенсивные культуры севооборота выносят основную доступную часть всего спектра не только макро, но и микроэлементов. '
            },
            {
              class: 'par',
              text: 'Лен особенно чувствителен к недостатку бора, что отрицательно сказывается как на закладке цветочных почек, так и на цветении. Кроме того, в случае дефицита доступного питания урожай будет еще меньше из-за низкой натуры масло-семян. Это обусловлено ухудшением состояния растения вызванного реутилизацией элементов питания из листьев и стебля: формируются легковесные семена и наступает преждевременное высыхание. Некорневые подкормки, особенно с калием, во многом позволяют решить эту проблему, стимулируя отток продуктов фотосинтеза, повышая его эффективность, а значит и урожайность.'
            },
          ],
          infoBot: [
            {
              class: 'par-ttl',
              text: 'Рекомендации'
            },
            {
              class: 'par2',
              text: 'Протравливание с «МЕГАМИКС – СЕМЕНА» дает дополнительное питание проростку, что крайне важно для мелких семян льна. Такое подспорье в питании активизирует начальное развитие культуры, повышая полевую всхожесть. '
            },
            {
              class: 'par2',
              text: 'Основные листовые подкормки оптимально проводить в фазы елочки и бутонизации выбирая препарат по ситуации: '
            },
            {
              class: 'par2',
              text: 'при внесении основных удобрений в почву и их доступности (наличие влаги) «МЕГАМИКС – ПРОФИ»; '
            },
            {
              class: 'par2',
              text: 'при нарушении корневого питания (почвенная засуха) «МЕГАМИКС – АЗОТ», «МЕГАМИКС – КАЛИЙ», «МЕГАМИКС – ФОСФОР»; '
            },
            {
              class: 'par2',
              text: 'в случае сильной засухи, особенно перед цветением — «МЕГАМИКС – КАЛИЙ»; '
            },
            {
              class: 'par2',
              text: 'при высокой интенсивности производства возрастает потребность и в мезоэлементах, которые можно внести с «МЕГАМИКС – СЕРА» и «МЕГАМИКС – МАГНИЙ-ЦИНК»; '
            },
            {
              class: 'par2',
              text: 'все удобрения МЕГАМИКС лучше комбинировать в баковой смеси с «МЕГАМИКС – БОР», поскольку лен — боролюбивая культура.'
            },
          ],
        },
      ],
      i: null
    }
  },
  mounted(){
    this.i = this.$route.params.id
    this.veget[this.i].bold = true;
    console.log(this.veget)
  },
  methods:{
    chooseProd(val){
          this.veget[this.i].bold = false;
          this.$router.push('/culture/'+val)
          this.i = val
          this.veget[this.i].bold = true;

      },
    goTo(val){
      this.$router.push(val)
    }
  }
}
</script>

<style>
.culture{
    padding: 0 5%;
}
.culture_block-img{
  margin-bottom: 20px;
  width: 100%;
}
.culture-info{
  position: relative;
  width: 100%;
  position: relative;
  vertical-align: top;
  display: inline-flex;
}
.main__block2{
    width: 66%;
    display: inline-block;
}
.culture__menu{
    width: 33%;
    display: inline-block;
    text-align: left;
    padding: 15px 0 0 60px;
}

  .culture__menu-item-img{
      display: inline-block;
  }
  .culture__menu-item{
    margin-bottom: 20px;
  }
  .culture__menu-item-name{
      display: inline-block;
      vertical-align: top;
      height: 100%;
  }
  .culture__menu-title{
    margin-bottom: 20px;
  }
  .culture__block-info-discription{
      text-align: left;
      font-size: 16px;
  }
  .culture-img{
      width: 70px;
      height: 70px;
      margin-bottom: 20px;
  }
  .tbl-elem{
      width: 100%;
      margin: 30px 0 30px 0;
  }
  .par-ttl{
      font-size: 24px;
      margin-bottom: 20px;
  }
  .par2{
    margin-bottom: 0px;
  }
  .culture__video{
    position: relative;
    width: 100%;
    display: inline-flex;
    margin: 60px 0;
  }
  .culture__video-block{
    width: 50%;
    text-align: left;
  }
  .culture__video-img{
      width: 95%;
  }
  .culture__video-title{
    margin-bottom: 20px;
  }
  .bold{
    font-weight: 500;
  }
  .square{
    width: 89px;
    height: 39px;
    display: inline-block;
    margin-top: 20px;
  }
  .square-text{
    display: inline-block;
  }
  .square-info{
    vertical-align: middle;
  }
  .product-text{
    padding: 25px 0 0 10px;
  }
</style>



