              text: 'Стимуляция развития (прохождения фаз) культуры, особенно на начальных этапах развития '
            },
            {
              class: 'par',
              text: 'Устранение азотно-фосфорного дисбаланса при избыточном азотном питании '
            },
            {
              class: 'par',
              text: 'Повышение посевных качеств семян и лежкости продукции '
            },
          ],
          video: false,
          table: "/static/tara/tables/fosfor-tbl.png"
        },
        {
          id: 7,
          name: 'КАЛИЙ',
          img: 'static/tara/kaliy1.png',
          imgBan: '/static/tara/kalliyBan.png',
          bold: false,
          info: [
            {
              class: 'par',
              text: '«МЕГАМИКС – КАЛИЙ» дополняет основное питание и временно его заменет, когда корневое питание затруднено почвенной засухой, низкой температурой почвы, стрессами, в частности от пестицидов. '
            },
            {
              class: 'par',
              text: 'Акцент на высокое содержание комплекса микроэлементов в «МЕГАМИКС – КАЛИЙ» позволяет данному удобрению повысить как эффективность калия в составе, так и стимулировать корневое питание и снятие стрессов. '
            },

            {
              class: 'par',
              text: 'Благодаря свойству калия связывать и структурировать воду в клетках растения обработки «МЕГАМИКС – КАЛИЙ» снижают повреждение морозами на переросших озимых культурах и способствуют удержанию влаги при засухе. '
            },
            {
              class: 'par',
              text: 'Одной из проблем интенсивных культур является снижение продуктивности в период сильной жары и засухи. Калий в засушливый период способствует разжижению клеточного сока и оттоку питательных веществ в запасающие органы.'
            },
            
          ],
          infoBot: [
            {
              class: 'par-ttl',
              text: 'Назначение «МЕГАМИКС – КАЛИЙ»:'
            },
            {
              class: 'par',
              text: 'Устранение признаков калийного голодания — краевой ожог, увядание '
            },
            {
              class: 'par',
              text: 'Обеспечение калийного питания в критические фазы развития культуры (особенно перед цветением) '
            },
            {
              class: 'par',
              text: 'Стимуляция ростовых процессов. Интенсивный рост часто совпадает с пиком поглощения калия (особенно на пропашных культурах) '
            },
            {
              class: 'par',
              text: 'Повышение выживаемости растений при стрессах погодного и антропогенного характера '
            },
            {
              class: 'par',
              text: 'Повышение количества и качества урожая (оттока ассимилятов) при засухе'
            },
          ],
          video: false,
          table: "/static/tara/tables/kaliy-tbl.png"
        },
      ],
      i: null
    }
  },
  mounted(){
    this.i = this.$route.params.id
    this.goods[this.i].bold = true;
  },
  methods:{
    chooseProd(val){
      this.goods[this.i].bold = false;
          //this.$router.push('/products/'+val)
          this.i = val
          this.goods[this.i].bold = true;
    },
    goTo(val){
      this.$router.push(val)
    }
  }
}
</script>

<style>
.product{
    padding: 0 5%;
}
.product_block-img{
  margin-bottom: 20px;
  width: 100%;
}
.product-info{
  position: relative;
  width: 100%;
  position: relative;
  vertical-align: top;
  display: inline-flex;
}
.main__block2{
    width: 66%;
    display: inline-block;
}
.product__menu{
    width: 33%;
    display: inline-block;
    text-align: left;
    padding: 15px 0 0 60px;
}
  .product__menu-item-img{
      display: inline-block;
  }
  .product__menu-item-name{
      display: inline-block;
      vertical-align: top;
      height: 100%;
  }
  .product__menu-title{
    margin-bottom: 20px;
  }
  .product__block-info-discription{
      text-align: justify;
      font-size: 16px;
  }
  .product-img{
      width: 70px;
      height: 70px;
      margin-bottom: 20px;
  }
  .tbl-elem{
      width: 100%;
      margin: 30px 0 30px 0;
  }
  .par-ttl{
      font-size: 24px;
      margin-bottom: 20px;
  }
  .par{
    margin-bottom: 20px;
  }
  .product__video{
    position: relative;
    width: 100%;
    display: inline-flex;
    margin: 60px 0;
  }
  .product__video-block{
    width: 50%;
    text-align: left;
  }
  .product__video-title{
    margin-bottom: 20px;
  }
  .tbl-elem__img{
    width: 100%;
  }
  .bold{
    font-weight: 500;
  }
  .product-text{
    padding: 20px 0 0 0;
  }
  .down{
    padding: 60px 0 0 0;
  }
</style>



