<template>
  <div class="header-menu2">
      <div class="header-menu2__collum">
          <img src="@/assets/logo.png" alt="" class="header-menu__logo-img link" @click="goTo('/')">
      </div>
      <div class="header-menu2__collum">
          <div class="header-menu">
              <div class="header-menu__item" v-for="(item, ind) of menuItems" :class="{reg: item.reg, bold: item.bold}" :key="item" @mouseenter ="showEl(ind)" @mouseout="hideEl(ind)" @click="routerTo(item.router, ind, curPage)">{{item.name}}</div>
          </div>
      </div>
      <div class="header-menu2__collum">
          <img class="header-menu2__mail" src="/static/img/phone.svg" alt="">
          <div class="header-menu2__phone">+7 (831) 592 73 52</div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'HeaderMenu2',
  data () {
    return {
        menuItems: [
            {
                name: 'О компании',
                bold: false,
                font: 'regular',
                items: ['string1','string2','string3'],
                show: false,
                router: '/About'
            },
            {
                name: 'Продукция',
                bold: false,
                font: 'regular',
                items: ['string11','string2','string3'],
                show: false,
                router: '/Products'
            },
            {
                name: 'Культуры',
                bold: false,
                font: 'regular',
                items: ['string12','string2','string3'],
                show: false,
                router: '/Cultures'
            },
            {
                name: 'Статьи',
                bold: false,
                font: 'regular',
                items: ['string13','string2','string3'],
                show: false,
                router: '/Articles'
            },
            {
                name: 'Документация',
                bold: false,
                font: 'regular',
                items: ['string14','string2','string3'],
                show: false,
                router: '/documentation'
            },
            {
                name: 'Контакты',
                bold: false,
                font: 'regular',
                items: ['string15','string2','string3'],
                show: false,
                router: '/contacts'
            }
        ],
        index: 0,
        curPage: null
    }
  },
  methods:{
      showEl(value, index){
          this.index = value
          this.menuItems[value].show = true;
      },
      hideEl(value){
          this.index = value
          this.menuItems[value].show = false;
      },
      routerTo(val, index, curPage){
          if (curPage === null){
              this.curPage = index
              this.menuItems[index].bold = true
          } else {
              this.menuItems[index].bold = true
              this.menuItems[curPage].bold = false
              this.curPage = index
          }
          console.log(this.menuItems)
          this.$router.push(val)
          
      },
      goTo(val){
      this.$router.push(val)
      
    }
  }
}
</script>


<style>


   .header-menu2{
       display: flex;
       margin: 40px 0 25px 0;
   }
   .header-menu2__collum{
       flex: 1;
   } 
   .header-menu2__collum:nth-of-type(2){
       flex: 3;
           margin-left: 50px;
   }
   .header-menu{
       display: flex;
       margin-top: 15px;
   }
   .header-menu__item{
       flex: 1;
       letter-spacing: 0;
       color: #4D4F51;
       font-size: 18px;
       
       /*font-weight: 300;*/
   }
   .header-menu__item:hover{
       cursor: pointer;
       font-weight: 500;
   } 
   .header-menu__logo-img{
       width: 245px;
       height: 45px;
       margin-left: 60px;
   }
   .header-menu2__phone{
       margin-top: 12px;
       font-size: 21px;
       color: #3BAC5D;
       font-weight: medium;
       display: inline-block;
   }
   .bold{
       font-weight: 500;
   }
   .header-menu2__mail{
       display: inline-block;
       margin: -5px 10px 0 0; 
   }
</style>


