                  text: 'ОПРЕДЕЛИТЕЛЬ ДЕФИЦИТА ЭЛЕМЕНТОВ ПИТАНИЯ'
              },  
          ]
        },
        {
          id: 6,
          title: 'Технология подкормок начинается с МЕГАМИКС-СЕМЕНА',
          img: 'static/articles/article0.png',
          bold: false,
          text: [
              {
                class: 'par',
                text: 'Не секрет, что дробное и локальное внесение удобрений дает наибольший эффект, чем их единовременное внесение. Этому учит и агрохимия, и рыночная экономика, требующая получить максимальную отдачу в рамках ограниченного бюджета. Но всегда возникает противоречие: чем локальнее и эффективнее внесение удобрений, тем меньше можно внести, не повредив культуре.Чтобы найти компромисс современная агрохимия даёт несколько различных инструментов.'
              },
              {
                  class: 'par',
                  text: 'Один из таких инструментов, это «подкормка - обработкой семян удобрениями», такими как МЕГАМИКС. В случае его применения, процесс перехода проростка с питания от запасов семени на корневое питание наступает намного быстрее. В составе МЕГАМИКС содержится NPK, в количестве достаточном для первых 2-3 недель начального развития культуры, непосредственно в зоне питания проростка. Это очень важно для поддержания его начального питания, пока корни не достигнут удобрений, внесённых при посеве. Кроме того, МЕГАМИКС это удобрение богатое микроэлементами - как по ассортименту, так и по их количеству. Для предпосевной обработки дозировка МЕГАМИКС составляет 2 л/т. Таким образом семена получают 172 грамма микроэлементов (Cu, Zn, Mo + 7 элементов) и 144 грамма мезоэлементов (S+Mg). Такая дозировка: безопасна для семени и богата элементами питания, которые достаточны и для растения, и для микрофлоры и не вызывают между ними конкуренцию за их потребление.'
              },
              {
                  class: 'par',
                  text: 'Обработкой семян, даёт ряд преимуществ для посева в целом. Повышаются полевая всхожесть и энергия прорастания. Это важно при заглублении посева в погоне за влагой или недостаточном контакте семян с почвой при сухом севе. Так мы получаем более развитый проросток. Его развитая корневая система способна усвоить больше основных удобрений, внесённых при посеве'
              },
              {
                  class: 'par',
                  text: 'Особенно это важно для тех, кто переходит на минимальные технологии. Когда с посевом вносится больше удобрений в зону, наиболее охватываемую корнями культуры и существует необходимость увеличения эффективности их усвоения. В этих условиях МЕГАМИКС позволяет реализовать более интенсивное питание культуры, благодаря увеличению активности ферментов и скорости биохимических реакций'
              },
              {
                  class: 'par',
                  text: 'МЕГАМИКС также полезен в технологиях с традиционной обработкой почвы, где по основному внесению удобрений существует возможность их применения в средних и больших количествах. Из-за их равномерного распределения по пахотному горизонту, растению сложнее их «собрать». Для этого необходима хорошая корневая развитая система, чему способствует МЕГАМИКС. Обхватывая большой объём почвы, обеспечивается наилучшее поглощение внесенных удобрений.'
              },
              {
                  class: 'par',
                  text: 'В настоящее время обязательным элементом интенсивной технологии возделывания любой культуры являются некорневые подкормки. Призванные дополнить основное внесение удобрений и максимально воздействовать на развитие культуры в ключевые фазы ее развития. Они увеличивают активность фотосинтеза, что дает культуре дополнительные ресурсы для активного почвенного питания. Дробное внесение основных удобрений по листу сильно ограничено концентрацией (1-2% для минеральных удобрений) и временем воздействия (2-3 недели), в то же время оно имеет максимальный КПД. Стоит понимать, что через лист культура поглощает пассивно, т.е. независимо от потребности в том или ином элементе в конкретную фазу. Например, для усвоения азотной подкормки, также нужны другие элементы. Иначе излишек азота будет выделен корнями. А значит, затраченные деньги на некорневую подкормку, сработают не так эффективно, как могли бы. Применение МЕГАМИКС в некорневых подкормах как совместно с основными удобрениями, так и раздельно от них, позволяет оптимизировать питание и повысить усвоение других удобрений, в т.ч. из почвы.'
              },
              {
                  class: 'par',
                  text: 'Отдельного внимания заслуживает влияние удобрений на озимые культуры. При посеве многие вносят фосфорные удобрения, чтобы сместить акцент развития культуры в сторону корней. Так она меньше перерастает, развивается хорошая корневая система, которая защищает от ряда негативных факторов перезимовки: выпирание, вмерзание в ледяную корку и прочих. Кроме фосфора с посевом, при предпосевной обработке семян рекомендуем применить совместно с протравителем микроэлементное удобрение с NPK МЕГАМИКС. Это дает на 1-2 дня ускорение прорастания всходов и ещё на 1-2 дня ускорение кущения, и способствует еще большему акценту на развитие корневой системы культуры совместно с развитием мощного узла кущения. При поздних сроках сева, такое ускорение начальных фаз - позволяет не повышать норму высева, рассчитанную на выпад и меньшее кущение. Это экономит предприятию «дополнительный рубль». При посеве в ранние сроки, МЕГАМИКС не увеличивает риск перерастания надземной части.'
              },
              {
                  class: 'par',
                  text: 'Для тех, кто применяет МЕГАМИКС на предпосевной обработке впервые особенно важно увидеть результат от препарата. Смотреть лучше всего, перед уходом в зиму: необходимо сорвать растение и сравнить с контрольным образцом. Растение с препаратом МЕГАМИКС будет иметь более толстый стебель, более широкий лист и темный окрас. Для того, чтобы увидеть разницу развития корней необходимо взять лопату. Из окна автомобиля разницу можно увидеть только на всходах… но эта фаза непродолжительна и ее можно увидеть, если ежедневно наблюдать за полем.'
              },
              {
                  class: 'par',
                  text: 'Технология применения МЕГАМИКС на пшенице состоит из предпосевной обработки семян и 2-х листовых подкормок, в идеале 3-х подкормок. Некорневые: 1-я в конец фазы кущения, 2-я по флаговому листу, 3-я на качество зерна - по колосу в фазу молочной спелости. 3-я обработка также важна в регионах подверженных засухе для снижения рисков высушивания зерна суховеями и обеспечения его налива. Дозировки зависят от целей конкретного предприятия. Например, если МЕГАМИКС применяется как антистрессант при гербицидной обработке, то достаточна дозировка 0,2 л/га; если задача подкормить пшеницу, то необходимо увеличить дозировку с учетом урожайности. Так, при запланированной урожайности пшеницы 40÷45 ц/га, общая дозировка при такой обработке необходима 0,4÷0,5 л/га. В случае применения МЕГАМИКС в полной технологии, общая потребность в нем составит: на предпосевную обработку 0,4÷0,5 л/га, на некорневые подкормки от 0,5 до 2,0 л/га. В технологии МЕГАМИКС особое внимание уделяется предпосевной обработке, доля которой составляет от 20 до 50%. Но это того стоит! Ради «сильного» кущения! Ради хорошего стеблестоя! Ради выживания культуры, в случае неблагоприятных условий, и для благополучного прохождения зимы!'
              },
          ]
        },
        {
          id: 7,
          title: 'МЕГАМИКС в условиях сухого сева.',
          img: 'static/articles/article0.png',
          bold: false,
          text: [
              {
                class: 'par',
                text: 'При обработке МЕГАМИКСОМ не происходит преждевременной стимуляции семян к прорастанию. Поскольку микроэлементы являются катализатором биологических реакций, то они действуют на зародыш, только когда семя само запустит этот механизм, т.е. начнёт вырабатывать ферменты и гормоны. Влага - один из основных факторов прорастания семян. Дыхание зерна злаков, а значит и биологическая активность, начинает возрастать при влажности более 15-16%, но микроэлементы первоначально проникают в семя пассивно с током воды, соответственно эффект от них зависит от степени увлажнения. Оптимальная влажность для прорастания порядка 50-60% и от веса зерновки, '
              },
              {
                  class: 'par',
                  text: 'Одна из опасностей сухого сева в недоступности основных удобрений из почвы. Когда проросток развивается только благодаря запасам питания в семени. В таких условиях особое значение приобретает NPK в составе МЕГАМИКС. Их небольшого количества проросткам хватает на первые 2-3 недели развития и в то же время микроэлементы активизируют процессы поступления питательных веществ из семени, что важно при недостаточном увлажнении эндосперма.'
              },
              {
                  class: 'par',
                  text: 'Такой комплексный подход МЕГАМИКС к питанию и стимулированию начального роста зародыша позволяет увеличить энергию прорастания, полевую всхожесть и ускорить начальные фазы развития, которые являются критическим периодом по устойчивости к осенней засухе. Поскольку чем больше биомасса, тем больше проросток может запасти в ней влаги. Этому же способствует и усиление фотосинтеза, сахара которого связывают воду внутри растения, снижая испарение, идут на рост растения и служат источником энергии и воды при дыхании растения.'
              },
          ]
        },
      ]
    }
  },
  methods: {
    chooseProd(val){
          this.$router.push('/article/'+val)
    },
    goTo(val){
      this.$router.push(val)
    }
  }
}
</script>

<style>
    .step{
        margin-left: 5%;
        font-weight: 100;
    }
    .articles{
      position: relative;
      width: 25%;
      display: inline-block;
      vertical-align: top;
      margin-bottom: 60px;
    }
    .articles-title{
      color:#707070;
      font-size: 24px;
      padding-right: 15%;
      text-align: justify;
      margin-top: 20px;
      
    }
    .articles-img{
      position: relative;
      width: 100%;
      height: 100%;
      padding-right: 8%;
    }
    
</style>



