<template>
  <div class="about">
    <div class="router-menu">Вы здесь: <div class="link" @click="goTo('/')"> Главная</div> / <div> О компании</div> </div>
    <div class="main__block-title left">О КОМПАНИИ</div>
    <div class="about__block">
        <div class="about__block-info">
            Огромное счастье заниматься любимым делом, особенно если ты твердо уверен в том,
            что результаты твоего труда крайне необходимы людям. Необходимы именно сегодня 
            для сохранения здоровья каждого человека, для решения общих и актуальных экологических
             проблем. Удобрения для сельского хозяйства, которые мы производим – это также наш вклад,
              пусть маленький, но, как мы считаем, очень своевременный в продовольственную безопасность России. <div class="br"></div> 
              Созданные на основе научных исследований, проверенные на полях сельских хозяйств Нижегородской, 
              Самарской, Ростовской областей, Республик Татарстан, Мордовия и еще более 20 регионов страны, наши 
              удобрения помогают труженикам села выращивать богатые урожаи и видеть вознаграждение за свой нелегкий труд. <div class="br"></div>
              Микроэлементный состав наших удобрений гарантирует сохранение и восстановление почвенного плодородия, 
              обеспечивает повышение иммунитета растений и устойчивость к различным заболеваниям и капризам погоды. <div class="br"></div>
              Твердо придерживаясь принципа «не навреди» природе, мы продолжаем работать с научными учреждениями для
               поиска более эффективных решений проблем плодородия почвы, использования новых экологически безопасных материалов и технологий.

        </div>
        <div class="about__block-info">

          <table class="foto-block">
            <tr>
              <td class="tara"><img src="/static/about/interv_2_10.png" alt="" class="img"></td>
              <td class="inter"><img src="/static/about/DSC09576.png" alt="" class="img"></td>
            </tr>
            <tr>
              <td colspan="2" class="plant"><img src="/static/about/interv_2_15.png" alt="" class="img"></td>
            </tr>
          </table>
        </div>
    </div>
    <div class="main__block-title left">ВИДЕО</div>
    <div class="main__block-subtitle left">Фильм о компании "МЕГАМИКС"</div> 
    
    <iframe class="about__video" width="100%" height="815" src="https://www.youtube.com/embed/862DMHi9sNA" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
    
   
  </div>
</template>

<script>
export default {
  name: 'About',
  methods:{
    
    goTo(val){
      this.$router.push(val)
    }
  }
}
</script>

<style>
  .about{
    width: 100%;
    text-align: center;
    padding-left: 5%;
    padding-right: 3%;
  }
  .about__block{
      display: flex;
      width: 100%;
  }
  .about__block-info{
      flex: 1;
      text-align: left;
      font-size: 18px;
      width: 50%;
    margin-right: 2%;
    margin-bottom: 30px;
  }
  .foto-block{
    position: relative;
    width: 100%;
  }
  .tara{
    position: relative;
    width: 33%;
    padding: 8px;
  }
  .inter{
    position: relative;
    width: 67%;
    padding: 8px;
  }
  .plant{
    position: relative;
    width: 100%;
    padding:8px
  }
  .img{
    position: relative;
    width: 100%;
    height: 100%;
  }
  .br{
    margin-bottom: 1rem;
  }
  iframe{
    margin: 30px 0 60px 0;
  }
  .about__video{
    padding: 0 3% 0 0;
  }
</style>



